
import request from '../../request'

//查询银行人员
export function query(data) {
    return request({
        url: '/eoms/bankWorkers/query',
        method: 'post',
        data,
        headers:{'content-type':'application/json'}
    })
}
//查询银行人员 分页
export function queryPage(data) {
    return request({
        url: '/eoms/bankWorkers/page',
        method: 'post',
        data,
        headers:{'content-type':'application/json'}
    })
}
//添加或更新银行人员
export function saveOrUpdate(data) {
    return request({
        url: '/eoms/bankWorkers/saveOrUpdate',
        method: 'post',
        data,
        headers:{'content-type':'application/json'}
    })
}

//银行人员绑定rfid
export function bindRfidBP(data) {
    return request({
        url: '/eoms/bankWorkers/bindRfid',
        method: 'post',
        data
    })
}
//银行人员解绑rfid
export function unbindRfidBP(data) {
    return request({
        url: '/eoms/bankWorkers/unbindRfid',
        method: 'post',
        data
    })
}

 //绑定银行人员照片
 export function bankWorkersBindPhoto(data) {
    return request({
      url: '/eoms/bankWorkers/bindPhoto',
      method: 'post',
      data
      
    })
  }
  

