<template>
  <div id="userManagement">
    <h3>用户管理</h3>
  </div>
</template>
<template>
  <div class="app-container" id="userManagement">
    <div class="filter-container">
      <el-input v-model="listQuery.keyword" placeholder="姓名/账号" class="filter-item" @keyup.enter.native="handleFilter" />
      <el-select v-model="listQuery.power" class="filter-item" placeholder="按权限筛选" multiple collapse-tags>
        <el-option v-for="item in powerOptions1" :key="item.id" :label="item.power" :value="item.id" />
      </el-select>
      <el-button class="filter-item" type="primary" @click="handleFilter" icon="el-icon-search">搜索</el-button>

      <el-button class="filter-item" type="primary" @click="handleCreate">注册</el-button>
    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="账号" width="150px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.username }}</span>
          </template>
        </el-table-column>
        <el-table-column label="姓名" width="150px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.name }}</span>
          </template>
        </el-table-column>
        <el-table-column label="身份证号" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.userCode }}</span>
          </template>
        </el-table-column>
        <el-table-column label="所属公司" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.company }}</span>
          </template>
        </el-table-column>

        <el-table-column label="权限" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ formatPower(scope.row.power) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="创建时间" width="190px" align="center">
          <template slot-scope="scope">
            <span>{{ formatDate1(scope.row.createTime) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="更新时间" width="190px" align="center">
          <template slot-scope="scope">
            <span>{{ formatDate1(scope.row.updateTime) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" fixed="right" min-width="400" align="center" class-name="small-padding fixed-width">
          <template slot-scope="{row}">
            <el-button class="modifyTeamBtn" v-if="power==1" :class="{'disabledBtn':(row.power!==2)}" type="primary" size="mini" :disabled="row.power!==2" @click="modifyTeams(row)">修改绑定大队</el-button>
            <el-button class="modifyTeamBtn" :class="{'disabledBtn':(row.power==1)}" type="primary" size="mini" :disabled="row.power==power" @click="modifyPass(row)">修改密码</el-button>
            <el-button type="primary" size="mini" :disabled="row.power==power" v-if="row.status==0" @click="handleFreezeAccount(row,1)">解冻</el-button>
            <el-button type="danger" size="mini" :disabled="row.power==power" v-if="row.status==1" @click="handleFreezeAccount(row,0)">冻结</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" :close-on-click-modal="false" center>
      <el-form :model="registerForm" status-icon :rules="registerRules" ref="registerForm" class="formStyle">
        <el-form-item label="角色" prop="power">
          <el-select v-model.number="registerForm.power" class="filter-item" placeholder="Please select" @change="clearValidate">
            <el-option v-for="item in powerOptions" :key="item.id" :label="item.power" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="人员" v-if="registerForm.power!==1&&registerForm.power!==2" prop="personId">
          <el-select v-model="registerForm.personId" placeholder="请输入关键词" @change="personChange(registerForm.personId)" filterable>
            <el-option v-for="item in personsOptions" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="账号" prop="username">
          <el-input v-model="registerForm.username"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="name" v-if="registerForm.power==1||registerForm.power==2">
          <el-input v-model="registerForm.name"></el-input>
        </el-form-item>
        <el-form-item label="身份证号" prop="userCode" v-if="registerForm.power==1||registerForm.power==2">
          <el-input v-model="registerForm.userCode"></el-input>
        </el-form-item>
        <el-form-item label="所属公司" prop="company">
          <el-input v-model="registerForm.company"></el-input>
        </el-form-item>

        <!-- <el-form-item label="人员类型" prop="personWorkType" v-if="registerForm.power==3">
          <el-select
            v-model="registerForm.personWorkType"
            class="filter-item"
            placeholder="Please select"
          >
            <el-option
              v-for="item in personWorkTypeOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item> -->

        <el-form-item label="大队" v-if="registerForm.power==2" prop="selectTeams">
          <el-select v-model="registerForm.selectTeams" clearable multiple placeholder="请输入关键词">
            <el-option v-for="item in largeTeamOptions" :key="item.id" :label="item.teamName" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input type="password" v-model="registerForm.pass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="password">
          <el-input type="password" v-model="registerForm.password" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="createData()">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog :visible.sync="dialogTeamFormVisible" title="修改绑定大队" center>
      <el-form :model="teamForm" ref="teamForm" class="formStyle" :rules="teamRules">
        <el-form-item label="大队" prop="modifyTeamsArr">
          <el-select v-model="teamForm.modifyTeamsArr" multiple filterable collapse-tags>
            <el-option v-for="item in largeTeamOptions" :key="item.id" :label="item.teamName" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="modifyTeamsRes()">确定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="修改密码" :visible.sync="dialogPassFormVisible" :close-on-click-modal="false" center>
      <el-form :model="passForm" status-icon :rules="passRules" ref="passForm" class="formStyle">

        <el-form-item label="新密码" prop="pass">
          <el-input type="password" v-model="passForm.pass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="newPwd">
          <el-input type="password" v-model="passForm.newPwd" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitModifyPass">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import { teamsSelect, personsSelect, } from "../../api/managementArchives/common"; //大队下拉框数据
import { query } from "../../api/managementArchives/bankPersonManagement"; //大队下拉框数据

import { register, userList, freezeAcount, bindTeams, modifyOtherPwd, managersQuery } from "../../api/user"; //注册，用户列表，冻结，修改绑定大队
import { formatDate } from "../../../src/common"; //格式化时间

let myLoading = undefined; // 弹窗服务调用
export default {
  name: "userManagement",
  components: { Pagination },
  data() {
    //密码自定义校验
    var validatePass11 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.passForm.pass !== "") {
          this.$refs.passForm.validateField("password");
        }
        callback();
      }
    };
    var validatePass22 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.passForm.pass) {


        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    //注册自定义验证
    const validateUsername = (rule, value, callback) => {
      if (value == "") {
        callback(new Error("请输入用户名"));
      } else {
        callback();
      }
    };
    const validatePassword = (rule, value, callback) => {
      if (value.length < 4) {
        callback(new Error("密码不能少于4位"));
      } else {
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.registerForm.password !== "") {
          this.$refs.registerForm.validateField("password");
        }
        callback();
      }
    };
    var validateName = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入姓名"));
      } else {
        if (this.IsChinese(value)) {
          callback(new Error("必须是中文"));
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.registerForm.pass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    var validateUserCode = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入身份证号"));
      } else if (
        !/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i.test(
          value
        )
      ) {
        callback(new Error("身份证号格式错误!"));
      } else {
        callback();
      }
    };
    const validateTeam = (rule, value, callback) => {
      if (value.length === 0) {
        callback(new Error('请选择大队'));
      } else {
        callback();
      }
    };
    return {
      //注册校验
      registerRules: {
        username: [
          {
            required: true,
            trigger: "blur",
            message: "请填写账号"
          }
        ],
        // selectTeams: [
        //   { trigger: "change", required: true, message: "请绑定大队" }
        // ],
        personWorkType: [
          { trigger: "change", required: true, message: "请选择人员类型" }
        ],
        personId: [
          { trigger: "change", required: true, message: "请选择人员" }
        ],
        name: [
          {
            required: true,
            trigger: "blur",
            validator: validateName
          }
        ],
        userCode: [
          {
            required: true,
            trigger: "blur",
            message: '请填写身份证号'
            // validator: validateUserCode
          }
        ],
        company: [
          {
            required: true,
            trigger: "blur",
            message: "请填写公司名称"
          }
        ],
        pass: [{ validator: validatePass, trigger: "blur", required: true }],
        power: [{ trigger: "change", required: true, message: "请选择角色" }],
        password: [
          { validator: validatePass2, trigger: "blur", required: true }
        ]
      },

      tableKey: 0,
      list: null, //用户列表
      total: 0, //用户数
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10,
        teamName: undefined,
        keyword: '',
        power: [],
      },
      indexMethod: 1, //序号
      personWorkTypeOptions: [
        { id: 1, name: "司机" },
        { id: 2, name: "组长" },
        { id: 3, name: "押运员" },
        { id: 4, name: "业务员" },
        { id: 5, name: "大队长" },
        { id: 6, name: "枪管员" },
        { id: 7, name: "车管员" },
        { id: 8, name: "库管员" }

      ], //权限下拉数据
      powerOptions: [
        { id: 1, power: "开发人员" },
        { id: 2, power: "用户管理员" },
        { id: 3, power: "车组工作人员" },
        { id: 4, power: "库包库管" },
        { id: 5, power: "银行工作人员" },
        { id: 6, power: "枪支管理员" },
        { id: 7, power: "车辆管理员" },
        { id: 8, power: "监控人员" },
      ],
      powerOptions1: [
        { id: 1, power: "开发人员" },
        { id: 2, power: "用户管理员" },
        { id: 3, power: "车组工作人员" },
        { id: 4, power: "库包库管" },
        { id: 5, power: "银行工作人员" },
        { id: 6, power: "枪支管理员" },
        { id: 7, power: "车辆管理员" },
        { id: 8, power: "监控人员" },
      ],
      personsOptions: [],

      dialogTeamFormVisible: false, //修改绑定大队弹窗显隐
      bindTeamsObj: {}, //要提交的修改绑定大队数据
      //注册数据
      registerForm: {
        username: "",
        name: "",
        userCode: "",
        company: "",
        pass: "",
        password: "",
        power: 2,
        selectTeams: [],
        personId: null,
        personWorkType: 1,
      },
      dialogFormVisible: false, //注册弹窗显隐
      dialogStatus: "", //注册（这个页面不存在编辑）
      textMap: {
        update: "编辑"
      },
      //修改大队数据
      teamForm: {
        modifyTeamsArr: []
      },
      largeTeamOptions: [], //大队下拉数据
      dialogPassFormVisible: false, //密码验证弹窗
      //修改密码数据
      passForm: {
        pass: "",
        newPwd: ""
      },
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限

      //修改密码验证规则
      passRules: {
        pass: [{ validator: validatePass11, trigger: "blur", required: true }],
        newPwd: [{ validator: validatePass22, trigger: "blur", required: true }]
      },
      teamRules: {
        modifyTeamsArr: [{ validator: validateTeam, trigger: "change", required: true }],
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      }
    };
  },
  created() {
    this.getList(); //用户列表
    this.getSelect(); //大队下拉数据
    if (this.power == 1) {
      this.powerOptions = [
        { id: 2, power: "用户管理员" },
        { id: 3, power: "车组工作人员" },
        { id: 4, power: "库包库管" },
        { id: 5, power: "银行工作人员" },
        { id: 6, power: "枪支管理员" },
        { id: 7, power: "车辆管理员" },
        { id: 8, power: "监控人员" },
      ]
      this.powerOptions1 = [
        { id: 1, power: "开发人员" },
        { id: 2, power: "用户管理员" },
        { id: 3, power: "车组工作人员" },
        { id: 4, power: "库包库管" },
        { id: 5, power: "银行工作人员" },
        { id: 6, power: "枪支管理员" },
        { id: 7, power: "车辆管理员" },
        { id: 8, power: "监控人员" },
      ]
    } else if (this.power == 2) {
      this.powerOptions = [
        { id: 3, power: "车组工作人员" },
        { id: 4, power: "库包库管" },
        { id: 5, power: "银行工作人员" },
        { id: 6, power: "枪支管理员" },
        { id: 7, power: "车辆管理员" },
        { id: 8, power: "监控人员" },
      ]
      this.powerOptions1 = [
        { id: 2, power: "用户管理员" },
        { id: 3, power: "车组工作人员" },
        { id: 4, power: "库包库管" },
        { id: 5, power: "银行工作人员" },
        { id: 6, power: "枪支管理员" },
        { id: 7, power: "车辆管理员" },
        { id: 8, power: "监控人员" },
      ]
    }


  },
  methods: {
    //点击搜素
    handleFilter() {
      if ((this.listQuery.keyword == "" || this.listQuery.keyword == undefined) && this.listQuery.power && this.listQuery.power.length == 0) {
        this.listQuery.keyword = null;
        this.getList();
        return;
      }
      let subData = {
        power: this.listQuery.power,
        status:this.$route.params.id

      }
      if (this.listQuery.keyword) {
        subData.keyword = this.listQuery.keyword
      }

      managersQuery(subData).then(res => {
        if (res.status == 200) {
          this.listQuery.page = 1;
          this.indexMethod = 1;
          this.listLoading = false;

          this.list = res.data;

          this.total = 0;
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    //点击修改密码
    modifyPass(row) {

      this.passForm = {
        userName: row.username,
        newPwd: '',
        pass: ''
      }

      this.dialogPassFormVisible = true;
      this.$nextTick(() => {
        this.$refs["passForm"].clearValidate();
      });
    },
    //修改密码提交
    submitModifyPass() {
      this.$refs["passForm"].validate(valid => {
        if (valid) {

          modifyOtherPwd({ userName: this.passForm.userName, newPwd: this.passForm.newPwd }).then(res => {
            if (res.status == 200) {
              this.dialogPassFormVisible = false;
              this.$message({
                message: res.msg,
                type: "success"
              });
            } else {
              this.$message.error(res.msg);
            }
          });
        } else {
          return false;
        }
      });
    },
    //获取用户列表
    getList() {
      let that = this;
      userList({
        page: this.listQuery.page,
        limit: this.listQuery.limit,
        status:this.$route.params.id
      }).then(res => {
        if (res.status == 200) {

          if (res.data && res.data.records && res.data.records.length) {
            if (this.power !== 1) {
              for (let i = res.data.records.length - 1; i >= 0; i--) {
                if (res.data.records[i].power == 1) {
                  res.data.records.splice(i, 1)
                }

              }
            }

          }
          that.listLoading = false;
          that.list = res.data.records;
          that.total = res.data.total;
          that.indexMethod =
            (that.listQuery.page - 1) * that.listQuery.limit + 1;
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    //大队下拉数据
    getSelect() {
      teamsSelect({ status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          this.largeTeamOptions = res.data;
        } else {
          if (res.data == null) {
            this.largeTeamOptions = [];

          }
          this.$message.error(res.msg);
        }
      });
      personsSelect({ work: -1, status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            this.personsOptions = [];
            return;
          }
          this.personsOptions = res.data;
        } else {
          if (res.data == null) {
            this.personsOptions = [];
          }
          this.$message.error(res.msg);
        }
      });
    },
    personChange(val) {
      let obj = {};
      obj = this.personsOptions.find((item) => {
        return item.id === val;
      });
      this.registerForm.name = obj.name;
      this.registerForm.userCode = obj.userCode;




    },
    clearValidate(val) {
      this.$nextTick(() => {
        this.$refs["registerForm"].clearValidate();
      });
      this.registerForm.name = "";
      this.registerForm.userCode = "";
      this.registerForm.personId = null;
      this.registerForm.selectTeams = [];
      if (val == 5) {
        query({ status: [0, 1] }).then(res => {
          if (res.status == 0) {
            if (res.data == null) {
              this.personsOptions = [];
              return;
            }
            this.personsOptions = res.data;
          } else {
            if (res.data == null) {
              this.personsOptions = [];
            }
            this.$message.error(res.msg);
          }
        });
      } else {
        let work = -1;
        switch (val) {
          case 3:
            work = -1;
            break;
          case 4:
            work = 8;
            break;

          case 6:
            work = 6;
            break;
          case 7:
            work = 7;
            break;
        }
        personsSelect({ work: work, status: 1 }).then(res => {
          if (res.status == 0) {
            if (res.data == null) {
              this.personsOptions = [];
              return;
            }
            this.personsOptions = res.data;
          } else {
            if (res.data == null) {
              this.personsOptions = [];
            }
            this.$message.error(res.msg);
          }
        });
      }

    },
    //重置注册窗口
    resetTemp() {
      this.registerForm = {
        username: "",
        name: "",
        userCode: "",
        company: "",
        pass: "",
        password: "",
        power: 2,
        personId: null,
        personWorkType: 1
      };
      if (this.power == 2) {
        this.registerForm.power = 3
      }
    },
    findItem(locations, key, val) {
      let obj = {};
      obj = locations.find((item) => {
        return item[key] === val;
      });
      return obj;
    },
    //注册
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["registerForm"].clearValidate();
      });
    },

    //注册提交
    createData() {
      this.$refs["registerForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let registerForm1 = {
            username: this.registerForm.username,
            name: this.registerForm.name,
            userCode: this.registerForm.userCode,
            company: this.registerForm.company,
            password: this.registerForm.password,
            power: this.registerForm.power
          };
          //如果是用户管理员需要提交绑定大队的数据
          if (this.registerForm.power == 2 && this.registerForm.selectTeams && this.registerForm.selectTeams.length) {
            registerForm1.teamIds = JSON.stringify(
              this.registerForm.selectTeams
            );
          }
          //如果用户是其他工作人员需要提交数据
          if (this.registerForm.power !== 1 && this.registerForm.power !== 2 && this.registerForm.power !== this.$userAdminPower) {
            registerForm1.personId = this.registerForm.personId;
            // registerForm1.personWorkType = this.registerForm.personWorkType;
            let obj = this.findItem(this.personsOptions, 'id', registerForm1.personId)
            registerForm1.name = obj.name;
            registerForm1.userCode = obj.userCode;
            // registerForm1.personWorkType = obj.workType;
          }
          register(registerForm1)
            .then(res => {
              if (res.status == 0) {
                this.listQuery.page = 1;
                this.getList();
                this.dialogFormVisible = false;
                this.$message({
                  message: res.msg,
                  type: "success"
                });
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },

    //判断是否是中文
    IsChinese(val) {
      var str = this.trim(val);

      if (str.length != 0) {
        var reg = /^[\u4E00-\u9FA5]+$/;
        if (!reg.test(str)) {
          return true;
        }
      }
    },

    trim(str) {
      //删除左右两端的空格
      return str.toString().replace(/(^\s*)|(\s*$)/g, "");
    },
    //冻结
    handleFreezeAccount(row, type) {
      let that = this;
      var freezeOrUnFreeze = {
        username: JSON.parse(this.$store.state.user.userInfo).UserName,
        targetname: row.username,
        frozen: type
      };
      freezeAcount(freezeOrUnFreeze)
        .then(response => {
          that.getList();
          that.$message({
            message: response.msg,
            type: "success"
          });
        })
        .catch(error => { });
    },
    //用户角色显示
    formatPower(power) {
      let text = "";
      switch (power) {
        case 1:
          text = '开发人员';
          break;
        case 2:
          text = '用户管理员';
          break;
        case 3:
          text = '车组工作人员';
          break;
        case 4:
          text = '库包库管';
          break;
        case 5:
          text = '银行工作人员';
          break;

        case 6:
          text = '枪支管理员';
          break;
        case 7:
          text = '车辆管理员';
          break;
        case 8:
          text = '监控人员';
          break;

      }
      return text;
    },
    //格式化时间
    myTime(date) {
      var arr = date.split("T");
      var d = arr[0];
      var darr = d.split("-");

      var t = arr[1];
      var tarr = t.split(".000");
      var marr = tarr[0].split(":");

      var dd =
        parseInt(darr[0]) +
        "/" +
        parseInt(darr[1]) +
        "/" +
        parseInt(darr[2]) +
        " " +
        parseInt(marr[0]) +
        ":" +
        parseInt(marr[1]) +
        ":" +
        parseInt(marr[2]);
      return dd;
    },
    formatDate1(time) {
      return formatDate(time, 1)
    },
    handleModifyPass(row) {
      this.dialogPasVisible = true;
      this.pasForm.userName = row.userName;

    },

    //点击修改大队
    modifyTeams(row) {
      this.dialogTeamFormVisible = true;
      this.bindTeamsObj.id = row.id;
      if (row.teamIds == null) {
        this.teamForm.modifyTeamsArr = [];
      } else {
        this.teamForm.modifyTeamsArr = JSON.parse(row.teamIds);
      }
    },
    //修改大队
    modifyTeamsRes() {

      this.bindTeamsObj.teamIds = JSON.stringify(this.teamForm.modifyTeamsArr);
      this.$refs["teamForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });

          bindTeams(this.bindTeamsObj)
            .then(res => {
              if (res.status == 200) {
                this.dialogTeamFormVisible = false;
                this.getList();
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    }
  }
};
</script>
<style lang="scss">
#userManagement {
  .modifyTeamBtn.el-button--mini {
    width: auto !important;
  }
  .disabledBtn {
    background-color: #b3b3b3;
    border-color: #b3b3b3;
  }
  .el-button--danger.is-disabled {
    background-color: #fab6b6;
    border-color: #fab6b6;
  }
}
</style>











